import React, { Component } from 'react';
import logo from './logo.svg';
import 'normalize.css'
import './reset.css'
import './App.css';
import TodoInput from './TodoInput'
import TodoItem from './TodoItem'
import UserDialog from './UserDialog'
import JSONDeepCopy from './JSONDeepCopy'
import {getCurrentUser, signOut, TodoModel} from './leanCloud'
//import AV from './leanCloud'
//import * as localStore from './localStore'


class App extends Component {
  constructor(props){
      super(props)
      this.state = {
          //leanCloudgetCurrentUser(user)state.user
          user:getCurrentUser() || {},
          newTodo: '',
          todoList: []
      }

      let user = getCurrentUser()
      if (user) {
        TodoModel.getByUser(user, (todos)=>{
          let stateCopy = JSONDeepCopy(this.state)
          stateCopy.todoList = todos
          this.setState(stateCopy)
        })
      }
  }

  render() {
    let todos = this.state.todoList
    .filter((item)=>!item.deleted)  //,deletedfalse
    .map((item,index)=>{
      return (
        <li key={index}>
          <TodoItem todo={item} 
            onToggle={this.toggle.bind(this)}
            onDelete={this.delete.bind(this)} />
        </li>
      )
    })

    return (
      <div className="App">
        <nav>
          <h1>{this.state.user.username||''} {/**/}
            {/*{this.state.user.id ? <button onClick={this.signOut.bind(this)}></button> : null}*/}
          </h1>
          {/**/}
          {this.state.user.id ? <a className="iconfont icon-tuichu" onClick={this.signOut.bind(this)}></a> : null}
        </nav>
        <div className = "input-w">
          <TodoInput content={this.state.newTodo} 
            onSubmit={this.addTodo.bind(this)} 
            onChange={this.changeTitle.bind(this)} />
        </div>
        <ol className="todo-list">
          {todos}
        </ol>
        {/*UserDialog*/}
        {this.state.user.id ? 
          null : 
          <UserDialog 
          onSignUp={this.onSignUpOrSignIn.bind(this)} 
          onSignIn={this.onSignUpOrSignIn.bind(this)}/>}
      </div>
    )
  }

  /**/
  componentDidUpdate(){
    //localStore.save('todoList', this.state.todoList)
  }

  /**/
  signOut(){
    signOut() /*leanCloudsignOut*/
    let stateCopy = JSONDeepCopy(this.state)
    stateCopy.user = {} /**/
    this.setState(stateCopy)
  }

  onSignUpOrSignIn(user){
    let stateCopy = JSONDeepCopy(this.state)
    stateCopy.user = user
    this.setState(stateCopy)
  }

  toggle(e,todo){
    let oldStatus = todo.status
    todo.status = todo.status === 'completed' ? '' : 'completed'
    console.log(this.state)
    
    TodoModel.update(todo,()=>{
      this.setState(this.state)
    },(error) =>{
      todo.status = oldStatus
      this.setState(this.state)
    })
  }

  delete(e,todo){
    TodoModel.destroy(todo.id, ()=>{
      todo.deleted = true
      this.setState(this.state)
    })
  }

  addTodo(event){
    console.log('Todo')

    let newTodo = {
      title:event.target.value,
      status:'',
      deleted:false
    }
    TodoModel.create(newTodo,(id)=>{
      newTodo.id = id
      this.state.todoList.push(newTodo)
      this.setState({
        newTodo: '',
        todoList: this.state.todoList
      })
    },(error)=>{
      console.log(error)
    })
  }


  changeTitle(event){
    this.setState({
      newTodo:event.target.value,
      todoList:this.state.todoList
    })
  }
}



export default App;




