//

import React, {Component} from 'react'
import './TodoItem.css'

class TodoItem extends Component{
	render(){
		return (
			<div className="TodoItem">
				{this.props.todo.status === 'completed' ? <a className="iconfont icon-yitianjia"></a> : null}
				<input type="checkbox" 
			      checked={this.props.todo.status === 'completed'}  //status'completed'checkedtrue
			      onChange={this.toggle.bind(this)}/>
			    <span className="title">{this.props.todo.title}</span>
			    {/*<button onClick={this.delete.bind(this)}></button>*/}
			    <span className="buttons">
				    <a className="iconfont icon-tianjia" onClick={this.toggle.bind(this)} title=""></a>
				    <a className="iconfont icon-shanchu" onClick={this.delete.bind(this)} title=""></a>
			    </span>
			</div>
	)}

	//
	toggle(e){
		this.props.onToggle(e, this.props.todo)
	}

	//
	delete(e){
		this.props.onDelete(e, this.props.todo)
	}	
}

export default TodoItem


